<template>
  <transition name="bounce">
    <div class="search-result-container">
      <!--  -->
      <b-scroll
        class="content-scroll"
        :pullup="true"
        v-if="searchResult.length"
        v-on:scrollEnd="scrollEnd"
      >
        <goods-list
          :goodsList="searchResult"
          :searchKeyword="searchKeyword"
          :isloadMore="isloadMore"
        ></goods-list>
      </b-scroll>

      <article class="empty-search-result" v-show="isEmptySearchResult">
        ~~
      </article>

      <!--  -->
      <div
        class="search-history-wrap"
        v-if="
          !searchKeyword && !isEmptySearchResult && searchHistoryList.length
        "
      >
        <section class="s-h-title">
          <span></span>
          <span class="s-h-clear" @click="DeleteSearchHisory"
            ><van-icon name="delete"
          /></span>
        </section>
        <section class="s-h-list">
          <span
            class="s-h-name"
            v-for="(item, index) in searchHistoryLimitList"
            :key="index"
            @click="clickSearch(item)"
          >
            {{ item }}
          </span>
          <p
            class="more"
            v-show="searchHistoryList.length > 5"
            @click="clickMore"
          >
            {{ limit === 5 ? "" : "" }}
            <van-icon v-if="limit === 5" name="arrow-down" />
            <van-icon v-else name="arrow-up" />
          </p>
        </section>
      </div>

      <article
        class="empty-search-history"
        v-show="
          !searchResult.length &&
            !searchHistoryList.length &&
            !isEmptySearchResult
        "
      >
        ~~
      </article>
      <!--  -->
      <loading :loadingStatus="searchStatus" type="spinner" />
    </div>
  </transition>
</template>

<script>
import GoodsList from "@/components/GoodsList";
import BScroll from "@/components/BScroll";
import { GoodsMixin } from "@/mixins/goodsMixin";

export default {
  name: "Search",
  mixins: [GoodsMixin],
  props: {
    searchResult: { type: Array, default: () => [] },
    searchKeyword: { type: String, default: "" }, // 
    searchStatus: { type: Boolean, default: false }, // 
    isEmptySearchResult: { type: Boolean, default: false }, // 
    isloadMore: { type: Boolean, default: false } // 
  },
  components: { GoodsList, BScroll },
  data() {
    return {
      limit: 5
    };
  },
  computed: {
    // 
    searchHistoryLimitList() {
      if (!this.searchHistoryList.length) return;

      return this.searchHistoryList.slice(0, this.limit);
    }
  },
  watch: {
    // 
    searchResult(newResult) {
      if (!newResult.length) return;
      // 
      this.searchKeyword && this.setSearchHistory(this.searchKeyword);
    }
  },
  methods: {
    /**
     * 
     */
    DeleteSearchHisory() {
      this.$dialog
        .confirm({
          title: "",
          message: "?",
          confirmButtonText: "",
          confirmButtonColor: "#F79709"
        })
        .then(() => {
          //  Vuex  Actions GoodsMixin 
          this.deleteSearchHistory();
        })
        .catch(() => null);
    },
    /**
     *  | 
     */
    clickSearch(searchKeyword) {
      this.$emit("click-search", searchKeyword);
    },
    /**
     *  |   
     */
    clickMore() {
      this.limit === 5
        ? (this.limit = this.searchHistoryList.length)
        : (this.limit = 5);
    },
    /**
     *  
     */
    scrollEnd() {
      this.$emit("scrollEnd");
    }
  }
};
</script>

<style lang="scss" scoped>
@import "./style.scss";
</style>
